import {ChangeDetectorRef, Component,OnInit,ViewEncapsulation} from '@angular/core';
import { Router } from '@angular/router';
import { fuseAnimations } from '@fuse/animations';
import { UtilityService } from 'app/shared/services/utility/utility.service';
/**
 * @title Basic use of the tab group
 */
@Component({
  selector: 'app-callback-crud',
  templateUrl: './callback-crud.component.html',
  styleUrls: ['./callback-crud.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations   : fuseAnimations
})
export class CallbackCrudComponent implements OnInit {
   
   tabLinks = [
    // { label: 'Open Request', link: 'list' },
    // { label: 'Close Request', link: 'closed' }
  ];
  localStorage: any;
  objectData: any;
  objectChildData: any;
  tempLinks = [];
  menuArray:any;
  
  constructor(private router: Router,private cdr: ChangeDetectorRef,private _utilityService:UtilityService) {
  }
  ngAfterViewInit(){
    this._utilityService.activeMenuItems$.subscribe(menuItemSelecte=> {
      this.menuArray = menuItemSelecte;
      if(this.menuArray == null || this.menuArray == undefined || this.menuArray.title == null || this.menuArray.title == undefined){
        this.menuArray  = JSON.parse( localStorage.getItem('activeMenuItems'));
      }
      this.cdr.detectChanges();
    })
     
  }

  ngOnInit(){
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    let nav_list = this.localStorage.nav_list_with_tab;
    nav_list.filter((x) => {
      if (x.id === "mangeCallback") {
        //this.menuArray = x;
        this.objectData = x;
        if(this.objectData.children != null && this.objectData.children != undefined){
          this.objectData.children.filter((y) => {
            if (y.id === "callback") {
              this.objectChildData = y;
              let isDefaultRoutePermeted = false;
              if(this.objectChildData.children != null && this.objectChildData.children!= undefined){
                this.objectChildData.children.filter((z) => {
                  if (z.id === "openRequest") {
                    isDefaultRoutePermeted = true;
                    this.tempLinks.push({ label: 'Open Request', link: 'list' });
                  }
                  if (z.id === "closeRequest") {
                    this.tempLinks.push({ label: 'Close Request', link: 'closed' });
                  }
                });
              }
             
              this.tabLinks = this.tempLinks;
              if (!isDefaultRoutePermeted) {
                this.router.navigate(['/mangeCallback/callback/crud/' + this.tempLinks[0].link]);
              }
  
            }
  
          });
        }
        
      }
    });
  }


  
}




@Component({
  selector: 'sunny-routed-content',
  template: 'Having a lot of light from the sun.',
})
export class SunnyTabContentComponent {}

@Component({
  selector: 'rainy-routed-content',
  template: 'A rainy period of time is one when it rains a lot',
})
export class RainyTabContentComponent {}

@Component({
  selector: 'foggy-routed-content',
  template: 'If the weather is foggy, there is fog',
})
export class FoggyTabContentComponent {}
