import { NgModule } from '@angular/core'; 
import { SharedModule } from 'app/shared/shared.module';
import { CallbackCrudComponent } from './callback-crud/callback-crud.component';
import { ClosedComponent } from './closed/closed.component';
import { ListComponent } from './list/list.component';
import { CallBackRoutingModule } from './callback-routing.module';
import { ActionDialogComponent } from './action-dialog/action-dialog.component';
import { FuseSharedModule  } from '@fuse/shared.module';
const COMPONENTS =[CallbackCrudComponent, ClosedComponent, ListComponent];
const COMPONENTS_DYNAMIC =[ActionDialogComponent];

@NgModule({
    imports:[SharedModule, CallBackRoutingModule,FuseSharedModule],
    declarations : [COMPONENTS,COMPONENTS_DYNAMIC],
    entryComponents : [COMPONENTS_DYNAMIC]
})

export class CallbackModule{}