import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CallbackCrudComponent } from './callback-crud/callback-crud.component';
import { TABS_DEMO_ROUTES } from './callback-crud/routes';

const routes : Routes = [
    {
        path : 'crud',component : CallbackCrudComponent, children : TABS_DEMO_ROUTES
    }
    
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class CallBackRoutingModule{}